.counter-and-timer-container {  
    margin: 0rem 0.3rem 0.3rem;
    display: flex;
    justify-content: space-between;
}

.question-counter-container,
.question-timer-container {
    font-size: 1rem; 
    font-family: 'Poppins', sans-serif;
}

@media (max-width: 600px) {

    .question-counter-container,
    .question-timer-container {
        font-size: 1.2rem; 
    }
}
